@font-face {
  font-family: "Avenir";
  src: url(./fonts/Avenir-Book.ttf);
  font-style: normal;
  font-weight: 300;
}

@font-face {
  font-family: "Avenir";
  src: url(./fonts/Avenir-Heavy.ttf);
  font-style: normal;
  font-weight: 800;
}

html {
  font-family: "Avenir", sans-serif;
}

* {
  box-sizing: border-box;
}

.search {
  position: relative;
  height: 70px;
  width: 100%;
  margin-top: 20px;
}

.search--small {
  height: 42px;
}

.icon {
  position: absolute;
  width: 19px;
  height: 19px;
  left: 26px;
  top: 0;
  bottom: 0;
  margin: auto;
}

.icon--small {
  width: 11px;
  height: 11px;
  left: 13px;
}

.input {
  font-family: inherit;
  background: #fff;
  border: 1px solid #e1e7ed;
  box-shadow: 0 1px 8px rgba(61, 78, 97, 0.1);
  border-radius: 4px;
  width: 100%;
  height: 100%;
  padding-left: 62px;
  font-size: 16px;
  font-weight: 800;
  color: #3d4e61;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}

.input--small {
  padding-left: 33px;
  font-size: 14px;
}

.input:hover {
  box-shadow: 0 3px 8px rgba(61, 78, 97, 0.2);
}

.input:focus {
  background: linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
  border-radius: 3px;
  outline: none;
}

.input::placeholder {
  color: #3d4e61;
  font-family: inherit;
  font-weight: 300;
}
